'use client';

import React from 'react';
import { timetable, subjects, faculty } from '@/lib/mock-data';
import type { TimeSlot } from '@/lib/types';
import { Button } from '@/components/ui/button';
import { Download, BellOff } from 'lucide-react';
import { useAuth } from '@/context/auth-context';
import { useToast } from '@/hooks/use-toast';
import { Card, CardContent } from '@/components/ui/card';

const daysOfWeek = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday'];
const timeLabels = ['09:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00'];

export default function TimetablePage() {
    const { role } = useAuth();
    const { toast } = useToast();

    const handleExport = (format: 'csv' | 'ics') => {
        toast({
            title: "Export Initiated",
            description: `Your timetable is being prepared for download as a ${format.toUpperCase()} file.`,
        });
    };
    
    const handleCancelClass = (slot: TimeSlot) => {
        const subject = subjects.find(s => s.id === slot.subjectId);
        toast({
            title: "Class Cancellation",
            description: `${subject?.name} at ${slot.startTime} has been cancelled. Students will be notified.`,
        });
    };

    return (
        <div className="flex flex-col gap-6">
            <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
                <div>
                    <h1 className="text-3xl font-bold">Weekly Timetable</h1>
                    <p className="text-muted-foreground">Your schedule for the upcoming week.</p>
                </div>
                <div className="flex gap-2 self-end md:self-center">
                    <Button variant="outline" onClick={() => handleExport('csv')}>
                        <Download className="mr-2 h-4 w-4" /> Export CSV
                    </Button>
                    <Button variant="outline" onClick={() => handleExport('ics')}>
                        <Download className="mr-2 h-4 w-4" /> Export ICS
                    </Button>
                </div>
            </div>
            
            <div className="overflow-x-auto rounded-lg border">
                <div className="grid grid-cols-[auto_repeat(5,1fr)] min-w-[1000px] bg-muted/20">
                    <div className="p-4 font-semibold text-center sticky left-0 bg-muted/50 z-10">Time</div>
                    {daysOfWeek.map(day => (
                        <div key={day} className="p-4 font-semibold text-center border-l">{day}</div>
                    ))}
                    
                    {timeLabels.map((time, timeIndex) => (
                        <React.Fragment key={time}>
                            <div className={`p-4 font-semibold h-28 flex items-center justify-center sticky left-0 bg-muted/50 z-10 ${timeIndex > 0 ? "border-t" : ""}`}>{time}</div>
                            {daysOfWeek.map(day => (
                                <div key={`${day}-${time}`} className={`p-1 h-28 border-l ${timeIndex > 0 ? "border-t" : ""}`}>
                                    {timetable
                                        .filter(slot => slot.day === day && parseInt(slot.startTime.split(':')[0]) === parseInt(time.split(':')[0]))
                                        .map(slot => {
                                            const subject = subjects.find(s => s.id === slot.subjectId);
                                            const fac = faculty.find(f => f.id === slot.facultyId);
                                            return (
                                                <Card key={slot.id} className="h-full w-full relative group shadow-sm transition-shadow hover:shadow-md" style={{ borderLeft: `4px solid ${subject?.color || 'transparent'}` }}>
                                                    <CardContent className="p-2 text-sm flex flex-col justify-between h-full">
                                                        <div>
                                                            <p className="font-bold text-card-foreground">{subject?.name}</p>
                                                            <p className="text-xs text-muted-foreground">{fac?.name}</p>
                                                        </div>
                                                        <p className="text-xs text-muted-foreground self-end font-mono">{slot.startTime} - {slot.endTime}</p>
                                                        {role === 'faculty' && (
                                                            <Button
                                                                variant="destructive"
                                                                size="icon"
                                                                className="absolute top-1 right-1 h-6 w-6 opacity-0 group-hover:opacity-100 transition-opacity"
                                                                onClick={() => handleCancelClass(slot)}
                                                            >
                                                                <BellOff className="h-3 w-3" />
                                                                <span className="sr-only">Cancel Class</span>
                                                            </Button>
                                                        )}
                                                    </CardContent>
                                                </Card>
                                            )
                                        })}
                                </div>
                            ))}
                        </React.Fragment>
                    ))}
                </div>
            </div>
        </div>
    );
}
